.adjust-container {
  margin: 1.5em;
  margin-right: 5em;
}
.adjust-container .adjust-box {
  max-height: 485px;
  width: 300px;
  pointer-events: auto;
}
.adjust-container .adjust-box md-list-item:not(.enabled) {
  color: #ccc;
}
.adjust-container .adjust-box md-list-item:not(.enabled) md-icon {
  color: #ccc;
}
