*:focus {
  border: 0;
  outline: 0;
}
html,
body {
  width: 100%;
  height: 100%;
  /*@media screen and (max-width: 600px){
      height: calc(~'100% + 32px');
    }*/
}
html {
  /* enough space to scroll up to get fullscreen on iOS8 */
}
body {
  padding-bottom: 0 !important;
  font-family: 'Gorgia';
  font-size: 16px;
  color: #606060;
  overflow-x: hidden;
  background: url(/images/geometry.png);
}
body > canvas {
  z-index: 1000;
  position: fixed;
  right: 10px;
  bottom: 10px;
}
body > md-content {
  position: relative;
}
body > md-content::before {
  content: '';
  width: 100%;
  left: 0;
  height: 1px;
  position: absolute;
  top: -1px;
  border-bottom: 1px solid #d7dddd;
  z-index: 61;
  transition: all 0.35s ease;
  transition-delay: 0.1s;
}
body.loggedIn > md-content::before {
  width: calc(100% - 47px);
  left: 47px;
}
@media screen and (max-width: 959px) {
  body.loggedIn > md-content::before {
    left: 0px;
    width: 100%;
  }
}
body > md-content {
  transition: all 1s ease-in-out;
  opacity: 1;
}
body md-toolbar.Header {
  transform: translateY(0px);
}
body:not(.startup) {
  overflow: hidden;
}
body:not(.startup) md-toolbar.Header {
  transform: translateY(-50px);
  z-index: 10;
}
body:not(.startup) > md-content {
  opacity: 0;
}
.Page-name {
  color: #006bb9;
}
.logo {
  color: #006bb9;
  font-size: 1.5em;
}
.info {
  color: #006bb9;
}
.info path {
  fill: #006bb9;
}
.icon-space {
  position: relative;
  top: 4px;
  left: 5px;
}
.inline-list li {
  display: inline;
}
.inline-list.menu-list li {
  position: relative;
  padding: 0 1em;
}
.inline-list.menu-list li::before {
  content: "/";
  position: absolute;
  right: 0;
  top: 0;
}
.inline-list.menu-list li:last-child::before {
  content: "";
}
.inline-list.menu-list li.active a {
  color: #006bb9;
}
.inline-list.menu-list li a {
  position: relative;
  color: #333333;
}
.inline-list.menu-list li a::after {
  content: '';
  position: absolute;
  height: 1px;
  width: 0;
  left: 50%;
  bottom: -5px;
  border-bottom: 1px solid #006bb9;
  transition: all 0.2s ease;
}
.inline-list.menu-list li a:hover::after {
  width: 100%;
  left: 0;
}
#hack {
  z-index: 100000;
  /* should be bigger than everything else */
  position: fixed;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  visibility: hidden;
  height: 0;
}
