  color: #fff;
  border-radius: 50%;
  font-size: 2em;
}
.icon-bubble.fisheries {
  background-color: #008c8c;
}
.icon-bubble.healthimpact {
  background-color: #ff9600;
}
.icon-bubble.airquality {
  background-color: #f7c80b;
}
.icon-bubble.watersanitation {
  background-color: #ff6d24;
}
.icon-bubble.waterresources {
  background-color: #7993f2;
}
.icon-bubble.agriculture {
  background-color: #009bcc;
}
.icon-bubble.forest {
  background-color: #2e74ba;
}
.icon-bubble.biodiversity {
  background-color: #00ccaa;
}
.icon-bubble.climate {
  background-color: #1cb85d;
}
p.small-info {
  color: #aaa;
  font-size: 0.9em;
}
p.small-info:hover {
  color: #006bb9;
}
.lower-text {
  font-size: 0.9em;
}
.rowed #map {
  left: 400px;
  width: calc(100% - 400px);
}
.rowed .Page {
  width: 400px;
  max-width: 400px;
  height: 100%;
}
.loose .Header {
  background: #fff !important;
}
.loose .Page {
  margin-left: calc(370px + 2em);
  margin-top: 1em;
  margin-right: 1em;
  transition: margin-left 0.25s ease-in-out;
  transition-delay: 0s;
  width: auto;
  position: absolute;
  max-height: calc(100% - 2em);
  max-width: calc(100% - 370px - 3em);
}
.loose .Page section.table {
  height: calc(100% - 2em);
}
.loose #sidebar {
  position: absolute !important;
  top: 1em;
  left: 1em;
  bottom: auto;
  max-height: calc(100% - 2em);
  overflow-x: hidden;
}
.loose.sidebar-closed .Page {
  transition-delay: 0.1s;
}
.loose #map {
  left: 0;
  width: 100%;
}
.loose.rowed .Page {
  width: 400px;
}
.loose .additional {
  top: 1em;
  height: calc(100% - 2em);
  left: calc(800px + 1em);
}
.loose.loggedIn #sidebar {
  left: calc(48px + 1em);
}
