div[ui-view='map'] {
  position: absolute;
  width: 100%;
  top: 0px;
  height: 100%;
}
#map {
  position: absolute;
  width: 100%;
  top: 0px;
  left: 0px;
  height: 100%;
  transition: all .6s ease;
}
@media screen and (max-width: 959px) {
  #map {
    height: calc(100% - 102px);
  }
}
.angular-leaflet-map {
  width: 100%;
  height: 100%;
  background-color: #ccdddd;
}
.greyed .angular-leaflet-map {
  background-color: #d9d9d9;
}
.greyed img.leaflet-tile {
  transition: filter 0.5s ease-in-out;
  filter: grayscale(1);
  -webkit-filter: grayscale(1);
  -moz-filter: grayscale(1);
  -o-filter: grayscale(1);
  -ms-filter: grayscale(1);
}
.country_info {
  background-color: #fff;
  padding: 00px;
  padding-right: 00px;
  position: fixed;
  bottom: 20px;
  right: 00px;
}
.label-icon-number {
  background-color: #6eafff;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 33px;
  box-shadow: 0px 0px 0px 5px rgba(255, 255, 255, 0.3);
}
.label-icon-number-lg {
  background-color: #6eafff;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-size: 14px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  line-height: 41px;
  box-shadow: 0px 0px 0px 5px rgba(255, 255, 255, 0.3);
}
.label-icon-text {
  display: inline-block;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  vertical-align: middle;
  text-shadow: 0px 1px 1px #232323;
  color: #ebf9f5;
}
/*.label-icon-text:hover {*/
/*color: #EDB229;*/
/*}*/
.label-icon-text-selected {
  color: #d9534f;
}
.label-icon-number-selected {
  box-shadow: 0px 0px 0px 7px rgba(255, 140, 0, 0.6);
}
.map-sub-menu {
  position: fixed;
  top: 9em;
  right: 1.5em;
  background: #fff;
  padding: .5em 0;
  width: 32px;
  text-align: center;
  cursor: pointer;
}
.noHeader .map-sub-menu {
  top: 6em;
}
.leaflet-top {
  top: 1em;
  left: auto;
  right: 1em;
  z-index: 2;
}
@media screen and (max-width: 959px) {
  .leaflet-top {
    top: auto;
    bottom: 3em;
  }
}
.leaflet-top .leaflet-control-zoom.leaflet-bar {
  margin-right: 1em;
  margin-left: 0px;
}
.leaflet-top .leaflet-control-zoom.leaflet-bar a {
  width: 32px;
  height: 32px;
  line-height: 32px;
  font-family: Georgia;
  font-weight: normal;
  color: #006bb9;
}
.leaflet-top .leaflet-control-zoom.leaflet-bar a.leaflet-disabled {
  color: #afafaf;
}
.leaflet-top .leaflet-control-zoom.leaflet-bar a:first-child {
  border-radius: 0px;
}
.leaflet-top .leaflet-control-zoom.leaflet-bar a:last-child {
  border-radius: 0px;
}
.leaflet-top .leaflet-control-home md-icon {
  font-size: 20px;
}
.leaflet-right .leaflet-control {
  display: none;
}
.leaflet-bar {
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
